@import 'vars';
@import 'components/text.scss';
@import 'components/button.scss';
@import 'components/fonts.scss';
.swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    &-bullet {
        width: 9.4px;
        height: 9.4px;
        background-color: $blue;
        opacity: 1;
        margin-left: 0 !important;
        &:not(:last-child) {
            margin-right: 15px !important;
        }
        &-active {
            border: 1px solid #474747;
            background-color: transparent;
        }
    }
}

.form-group {
    position: relative;
    margin: 0;
    margin-bottom: 30px;
    input {
        border: none;
        border-bottom: 1px solid #000000;
        padding: 0 10px;
        height: 47px;
        width: 100%;
        font-size: 18px;
        background-color: transparent;
        color: #000;
        &::placeholder {
            color: #000;
        }
    }
}

.check {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                // background-color: $green;
                border: 3px solid #fff;
            }
        }
        &:disabled+label {
            color: #AAAAAA;
            pointer-events: none;
        }
    }
    label {
        padding-left: 40px;
        position: relative;
        min-height: 20px;
        font-size: 14px;
        line-height: 20px;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border-radius: 2px;
            background: rgba(255, 255, 255, 0.01);
            border: 1px solid rgba(255, 255, 255, 0.45);
            width: 20px;
            height: 20px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            transition: all .100s;
        }
    }
}

@media screen and (max-width: 768px) {
    .form-group {
        input {
            padding: 0;
            font-size: 15px;
            height: 42px;
        }
    }
}